import React from "react";
import styleContainer from '../../common/style/Container.module.css'
import s from './MyProjects.module.scss'
import {Project} from "./project/Project";
import {Title} from "../../common/title/Title";
import SocialNetwork from '../../assets/image/SocialNetwork.jpg'
import Todo from '../../assets/image/TodoList.jpg'
import Flower from '../../assets/image/flower.jpg'
import Counter from '../../assets/image/Counter.jpg'

export const MyProjects = () => {
    const social = {
        backgroundImage: `url(${SocialNetwork})`
    };

    const todo = {
        backgroundImage: `url(${Todo})`
    }
    const flower = {
        backgroundImage: `url(${Flower})`
    }

    const counter = {
        backgroundImage: `url(${Counter})`
    }

    return (
        <div className={s.myProjectBlock} id={"portfolio"}>
            <div className={`${styleContainer.container} ${s.projectContainer}`}>
                <Title text={"Projects"} color={"Black"}/>
                <div className={s.myProjects}>
                    <Project style={social} title={"The Social Network"} description={"Project implemented on: React, Type Script, Redux," +
                    " RestAPI, Thunk, Material UI, Unit Tests."}/>
                    <Project style={todo} title={"Todolist"} description={"Project implemented on: React,Type Script, Redux, RestAPI, Thunk, Material UI," +
                    " Unit Tests, StoryBook"}/>

                    <Project style={flower} title={"Flower market"} description={"...in progress"}/>

                    <Project style={counter} title={"Counter"} description={"My first project with React, Redux,Material UI, Type Script."}/>
                </div>
            </div>
        </div>
    )
}