import React from "react";
import s from './Contacts.module.css'
import styleContainer from '../../common/style/Container.module.css'
import {Title} from "../../common/title/Title";
import {ContactInfo} from "../../common/ContactInfo/CoontactInfo";


export const Contacts = () => {
    return (
        <div className={s.contactsBlock} id={"contacts"}>
            <div className={`${styleContainer.container} ${s.contactContainer}`}>
                <Title text={"Contacts"} color={"white"}/>
                    <div className={s.contactsWrapper}>

                        <ContactInfo/>

                        <div style={{width:"600px",marginBottom: "50px"}}>
                            <form className={s.form}>
                                <div className={s.inputBlock}>
                                    <input placeholder={"Your Name"} className={s.input}/>
                                    <input placeholder={"Your Email"} className={s.input}/>
                                </div>
                                <textarea placeholder={"Your Message"} className={s.textarea}/>
                            </form>
                            <div style={{justifyContent:"end", height:"100%", display:'flex'}}>
                                <button className={s.send}>Отправить</button>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
    )
}